import React from "react";
import { Card, Grid, Row, Text, Button } from "@nextui-org/react";
import { connect } from "react-redux";
import { ACTIONS } from "../action";

function CardItem({ name, price, imgurl, addToCart, id }) {
  console.log("CardItem");

  const handleAddToCart = () => {
    addToCart({
      id,
      name,
      price,
      imgurl,
      item_count: 1
    });
  };
  return (
    <Grid xs={6} sm={3}>
      <Card hoverable clickable>
        <Card.Body css={{ p: 0 }}>
          <Card.Image
            objectFit="cover"
            src={imgurl}
            width="100%"
            height={140}
            alt={name}
          />
        </Card.Body>
        <Card.Footer justify="flex-start">
          <Row wrap="wrap" justify="space-between">
            <Text b>{name}</Text>
            <Text css={{ color: "$accents4", fontWeight: "$semibold" }}>
              {price}
            </Text>
            <Button size="xs" onClick={handleAddToCart}>
              Add to cart
            </Button>
          </Row>
        </Card.Footer>
      </Card>
    </Grid>
  );
}

const mapDisptachToProps = (dispatch) => {
  return {
    addToCart: (payload) => {
      dispatch({
        type: ACTIONS.ADD_TO_CART,
        payload: payload
      });
    }
  };
};

export default connect(null, mapDisptachToProps)(CardItem);
